@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;600&display=swap);
.App {
  background: linear-gradient(
    135deg,
    rgb(34, 184, 184) 0%,
    rgb(34, 184, 184) 50%,
    rgb(192, 54, 107) 50%,
    rgb(192, 54, 107) 100%
  );
  widows: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.joke-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: 10px;
  border-bottom: 1px solid rgba(148, 148, 148, 0.26);
}

.emoji i {
  font-size: 26px;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(138, 138, 138, 0.562);
  border-radius: 50%;
}

.counter {
  width: 20%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.counter button {
  margin: 3px;
}

.joke-text {
  width: 75%;
  font-family: "Poppins";
  font-size: 14px;
}

.emoji {
  width: 10%;
}

.emoji:hover {
  -webkit-animation: spin 1s ease-in;
          animation: spin 1s ease-in;
}

.circle {
  border: 3px solid rgb(224, 68, 7);
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: white;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(145, 145, 145, 0.555);
  font-family: "Poppins";
}

i {
  color: black;
  margin: 5px;
  cursor: pointer;
  color: rgba(148, 148, 148, 0.767);
}

.up:hover {
  -webkit-animation: move-up 0.5s ease-in;
          animation: move-up 0.5s ease-in;
  color: #4caf50;
}

.down:hover {
  -webkit-animation: move-down 0.5s ease-in;
          animation: move-down 0.5s ease-in;
  color: #f44336;
}

@-webkit-keyframes move-down {
  0%,
  50%,
  75% {
    transform: translateY(3px);
  }
  25%,
  100% {
    transform: translateY(0);
  }
}

@keyframes move-down {
  0%,
  50%,
  75% {
    transform: translateY(3px);
  }
  25%,
  100% {
    transform: translateY(0);
  }
}

@-webkit-keyframes move-up {
  0%,
  50%,
  75% {
    transform: translateY(-3px);
  }
  25%,
  100% {
    transform: translateY(0);
  }
}

@keyframes move-up {
  0%,
  50%,
  75% {
    transform: translateY(-3px);
  }
  25%,
  100% {
    transform: translateY(0);
  }
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  30% {
    transform: rotate(40deg);
  }

  50% {
    transform: rotate(0deg);
  }

  80% {
    transform: rotate(20deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  30% {
    transform: rotate(40deg);
  }

  50% {
    transform: rotate(0deg);
  }

  80% {
    transform: rotate(20deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.container {
  width: 70%;
  margin: 0 auto;
  height: 80vh;

  display: flex;
}

.left {
  width: 30%;
  position: relative;
  z-index: 5;
  background: rgb(104, 15, 163);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.404);
}

.left img {
  width: 200px;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.336);
  border-radius: 50%;
  margin-bottom: 3rem;
}

.left h1 {
  color: white;
  font-family: "Poppins", sans-serif;
}

.light {
  font-weight: 200;
}

.rigth {
  position: relative;
  width: 75%;
  height: 85%;
  align-self: center;
  background: white;
  box-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.336);
  border-radius: 5px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.btn {
  padding: 1rem 2rem;
  background: linear-gradient(
    135deg,
    rgb(23, 226, 226) 0%,
    rgb(35, 226, 226) 50%,
    rgb(192, 54, 107) 50%,
    rgb(192, 54, 107) 100%
  );
  border: none;
  border-radius: 10px;
  color: white;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: "Poppins";
  box-shadow: 0.5rem 0.5rem 0.5rem rgba(0, 0, 0, 0.336);
  cursor: pointer;
  transition: 0.3s ease;
  font-weight: 500;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: translateY(5px);
  box-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.336);
  color: black;
}

.loading {
  font-family: "Poppins";
}

.loading-cont {
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

